<template>
  <el-table
      :data="tableData"
      stripe
      style="width: 100%">
    <el-table-column
        prop="index"
        label="序号"
        width="180">
    </el-table-column>
    <el-table-column
        prop="name"
        label="课程名"
        width="180">
    </el-table-column>
    <el-table-column
        prop="teacher"
        label="教师名">
    </el-table-column>
    <el-table-column
        prop="classroom"
        label="教室">
    </el-table-column>
    <el-table-column
        prop="selectedCount"
        label="已选人数">
    </el-table-column>
    <el-table-column
        prop="maxCount"
        label="人数上限">
    </el-table-column>
    <el-table-column
        prop=""
        label="操作">
      <template slot-scope="scope">
        <el-button size="mini" type="primary" plain :data-id="scope.row.courseId"
                   @click="updateCourse(scope.row.courseId)"
                   class="addScore">编辑
        </el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
  import updateCourseBox from "./updateTeacherCoursesBox.vue";

  export default {
    name: 'allCourses',
    methods: {
      updateCourse(id) {
        const h = this.$createElement;
        this.$msgbox({
          title: '消息',
          message: h(updateCourseBox, {ref: 'ruleForm'}),
          showCancelButton: true,
          showConfirmButton: true,
          beforeClose: (action, instance, done) => {
            if (action === 'confirm') {
              if (this.$refs.ruleForm.submitForm('ruleForm')) {
                instance.confirmButtonLoading = true;
                instance.confirmButtonText = '修改中...';
                setTimeout(() => {
                  done();
                  setTimeout(() => {
                    instance.confirmButtonLoading = false;
                  }, 300);
                }, 3000);
              }
            } else {
              done();
            }
          }
        }).then(action => {
          this.$message({
            type: 'info',
            message: 'action: ' + action
          });
        });
      }
    },
    data() {
      return {
        tableData: [{
          index: '1',
          name: '王小虎',
          teacher: '张5',
          classroom: '5教',
          selectedCount: 20,
          maxCount: 34,
          selected: true,
          courseId: 1,
        }, {
          index: '2',
          name: '王小虎2',
          teacher: '张5',
          classroom: '5教',
          selectedCount: 65,
          maxCount: 89,
          selected: false,
          courseId: 2,
        }, {
          index: '3',
          name: '王小虎3',
          teacher: '张5',
          classroom: '5教',
          selectedCount: 34,
          maxCount: 90,
          selected: true,
          courseId: 3,
        }, {
          index: '4',
          name: '王小虎4',
          teacher: '张5',
          classroom: '5教',
          selectedCount: 76,
          maxCount: 100,
          selected: false,
          courseId: 4,
        }]
      }
    }
  }
</script>